.App{
  display: grid;
  grid-template: minmax(30px , 40px)  1fr minmax(50px , 100px) / 1fr minmax(100px , 150px) ;
  grid-template-areas: "Header Header" "Main Sidebar" "Footer Footer";
  grid-gap: 10px;
  min-height: 100vh;
  padding: 0 15px 0 15px;
}
.App > header , .App > main , .App > aside , .App >footer{
  border-radius: 5px;
  border: 1px solid #eee;
}
.App > header{
  grid-area: Header;
}
.App > main{
  grid-area: Main;
}
.App > aside{
grid-area: Sidebar;
}
.App >footer{
grid-area: Footer;
}
